@import '../../../theme/theme-default.less';
@st-prefix: ~'.st';

@{st-prefix} {
  display: block;
  &__p-left {
    .@{ant-prefix}-table-pagination {
      display: block;
      float: none;
      text-align: left;
    }
  }
  &__p-center {
    .@{ant-prefix}-table-pagination {
      display: block;
      float: none;
      text-align: center;
    }
  }
  // block
  &__btn {
    &-sub {
      @{st-prefix}__btn-text {
        color: inherit;
      }
    }
    &-disabled {
      @{st-prefix}__btn-text {
        color: @st-btn-disabled-color;
        pointer-events: none;
      }
    }
  }
  // 
  .nz-resizable-handle {
    display: flex;
    align-items: center;
    justify-content: center;
    > i {
      width: 1px;
      height: 60%;
      background: @border-color-split;
    }
  }
  .nz-resizable-preview {
    border-width: 0;
    border-right-width: 1px;
  }
  //  `nz-card`  `mb-md` 
  .@{ant-prefix}-table ~ nz-pagination > .@{ant-prefix}-pagination {
    margin-bottom: 0;
  }

  &__filter {
    &-keyword {
      padding: @layout-gutter;
    }
  }
  // Title
  .@{ant-prefix}-table-column-title {
    display: flex;
    align-items: center;
  }
  &__head {
    &-optional,
    &-tip {
      margin-left: 2px;
      color: @st-title-optional-color;
    }
  }
  // 
  .@{ant-prefix}-table-thead > tr > th .@{ant-prefix}-table-filter-selected.@{ant-prefix}-table-filter-icon {
    color: @primary-color;
  }
  // 
  &__checkall-selection {
    display: inline-block;
    margin-left: 4px;
  }
  //  `st` BUG
  .@{ant-prefix}-table-tbody > tr {
    .@{ant-prefix}-table-wrapper:only-child {
      //  https://github.com/ng-alain/ng-alain/issues/1619
      .ant-table-tbody > tr:last-child > td {
        border-bottom: @border-width-base @border-style-base @border-color-split;
      }
      //  https://github.com/ng-alain/ng-alain/issues/1608
      .@{ant-prefix}-table {
        margin: 0;
      }
    }
  }
  // 
  .@{ant-prefix}-table {
    &-cell-fix-left-first::after,
    &-cell-fix-left-last::after {
      transform: translateX(80%);
    }
    &-cell-fix-right-first::after,
    &-cell-fix-right-last::after {
      transform: translateX(-80%);
    }
  }

  @media screen and (min-width: @nz-table-rep-min-width) {
    &__width {
      &-strict {
        .ant-table-content,
        .ant-table-body {
          > table {
            word-wrap: break-word !important;
            table-layout: fixed !important;
          }
        }
      }
    }
  }
}
